          <div style="height: 80%;">
            <div style="font-size: 2vh;font-weight: 600;text-align: left;">
              <span>1.Variables</span>
            </div>
            <div>
              <el-form :model="categoryType" label-position="left" >
                <el-row :gutter="20">
                  <el-col :span="9">
                    <span class="optionSpan">Audio</span>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="AudioFeature">
                      <el-switch v-model="categoryType.AudioFeature" ></el-switch>
