                  </el-col>
                  <el-col :span="9">
                    <span  class="optionSpan">Guesture</span>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="Denseflow">
                      <el-switch v-model="categoryType.Denseflow" ></el-switch>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="9">
                    <span class="optionSpan">Image</span>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="ImageBackground">
                      <el-switch v-model="categoryType.ImageBackground" ></el-switch>
                    </el-form-item>
                  </el-col>
                  <el-col :span="9">
                    <span class="optionSpan">FacialLandmarkd</span>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="FacialLandmark">
                      <el-switch v-model="categoryType.FacialLandmark" ></el-switch>
                    </el-form-item>
