                </el-row>
                <el-row :gutter="20">
                  <el-col :span="9">
                    <el-col :span="9">
                    <span class="optionSpan">FacialDescription</span>
                  </el-col>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="FacialDescription">
                      <el-switch v-model="categoryType.FacialDescription" ></el-switch>
                    </el-form-item>
                  </el-col>
                </el-row>
                
              </el-form>
            </div>
            <div style="font-size: 2vh;font-weight: 600;text-align: left;">
              <span>2.Features</span>
            </div>
            <div>
              <el-form :model="categoryType" label-position="left" >
                <el-row :gutter="20">
                  <el-col :span="9">
                    <span class="optionSpan">Audio</span>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="FeatureAudio">
                      <el-switch v-model="categoryType.FeatureAudio" ></el-switch>
                    </el-form-item>
                  </el-col>
                  <el-col :span="9">
                    <span class="optionSpan">Image</span>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="FeatureImagk">
                      <el-switch v-model="categoryType.FeatureImage" ></el-switch>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="9">
                    <span class="optionSpan">Text</span>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="FeatureText">
                      <el-switch v-model="categoryType.FeatureText" ></el-switch>
                    </el-form-item>
                  </el-col>
                </el-row>
              </el-form>
            </div>
            <div style="font-size: 2vh;font-weight: 600;text-align: left;">
              <span>3.Emotion</span>
            </div>
            <div>
              <el-form :model="categoryType" label-position="left" >
                <el-row :gutter="20">
                  <el-col :span="9">
                    <span class="optionSpan">IsAnalysis</span>
                  </el-col>
                  <el-col :span="3">
                    <el-form-item label="" prop="FacialDescription">
                      <el-switch v-model="categoryType.IsAnalysis" ></el-switch>
                    </el-form-item>
                  </el-col>
                </el-row>
              </el-form>
            </div>
          </div>
          
          <div style="display: block;">
            <el-button type="primary" @click="submitForm('ruleForm')">Submit</el-button>
            <el-button @click="resetForm('ruleForm')">Reset</el-button>
          </div>
          
        </el-card>
      </el-col>
      <el-col :span="16" v-if="showTable" v-loading="dataIsLoading" :element-loading-text="dataLoadingText" element-loading-background="rgba(0,0,0,0.8)">
        <el-card class="table1" style="height: 44vh;overflow: scroll;">
          <div slot="header">
            <span>Variables Calculation</span>
            <a href="{{apiBaseUr}}/data/download?fileType=variable">
              <el-button style="float: right; padding: 3px 0" type="text" >DownLoad</el-button>
            </a>
          </div>
          <div >
            <el-table :data="table1" height="280">
              <el-table-column label="Variable" prop="Variable" ></el-table-column>
          
              <el-table-column label="Categroy" prop="Categroy" ></el-table-column>
            
              <el-table-column label="Calculate Time" prop="CalculateTime" width="200px"></el-table-column>
        
              <el-table-column label="Value" prop="Value" ></el-table-column>
            </el-table>
          </div>
          <div class="block">
              <el-pagination
                @current-change="handleCurrentChange"
                :current-page="page.pageNum"
                :page-size="5"
                background 
                layout="total,prev, pager, next"
                :total=page.pageTotal>
              </el-pagination>
            </div>
        </el-card>
        <el-card style="margin-top: 1vh;height: 31vh;overflow-y: scroll;">
          <div slot="header">
            <span>Feature Extraction</span>
            <a href="{{ apiBaseUr }}/data/download?fileType=feature">
              <el-button style="float: right; padding: 3px 0" type="text">DownLoad</el-button>
            </a>
          </div>
          <div>
            <el-table :data="table2" style="height: 190px;">
              <el-table-column label="Categroy" prop="Categroy" ></el-table-column>
          
              <el-table-column label="Tool" prop="Tool" ></el-table-column>
            
              <el-table-column label="Frequency" prop="Frequency" ></el-table-column>
        
              <el-table-column label="Size" prop="Size" ></el-table-column>
            </el-table>
          </div>
        </el-card>
        <el-card style="margin-top: 1vh;height: 11vh;" class="box-card" v-show="checkCategoryType.IsAnalysis">
          <div slot="header">
            <span class="demonstration">Emotion Analysis</span>
          </div>
          <div style="width: 60%;margin-left: 20%;float: left;">
            <el-progress :percentage="percent" :stroke-width="22" :color="colors" :format = "format" :text-inside ="true" text-color="white" ></el-progress>
          </div>
          <div style="float: left;margin-left: 2%;"><span>Grade:{{ percent }}</span></div>
        </el-card>
