<script>
// @ is an alias to /src
import axios from 'axios'
import moment from 'moment'
const apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
axios.defaults.withCredentials = true;
export default {
  name: 'HomeView',
  data(){
    return{
      categoryType:{AudioFeature:true,Denseflow:true,ImageBackground:true,FacialLandmark:true,FacialDescription:true,FeatureAudio:true,FeatureImage:true,FeatureText:true,IsAnalysis:true},
      checkCategoryType:{AudioFeature:true,Denseflow:true,ImageBackground:true,FacialLandmark:true,FacialDescription:true,FeatureAudio:true,FeatureImage:true,FeatureText:true,IsAnalysis:true},
      table1:[
      ],
      table1Org:"",
      page:{
        pageTotal:0,
        pageNum:1,
      },
      table2:[
      ],
      progressText:"",
      showTable:false,
      percent:0,
      colors: [
          {color: '#1A8CFF', percentage: 10},
          {color: '#4DA6FF', percentage: 20},
          {color: '#80BFFF', percentage: 30},
          {color: '#B3D1FF', percentage: 40},
          {color: '#E0F0FF', percentage: 50},
          {color: '#CCE5FF', percentage: 60},
          {color: '#99CCFF', percentage: 70},
          {color: '#66B2FF', percentage: 80},
          {color: '#3399FF', percentage: 90},
          {color: '#0000FF', percentage: 100}
        ],
        showFile:false,
        showPercent:false,
        uploadPercent:0,
        dataIsLoading:false,
        dataLoadingText:""
    }
  },
  mounted(){
    this.getCookie();
  },
  methods:{
    submitForm(){
      this.checkCategoryType = {...this.categoryType};
      this.dataIsLoading = true
      this.showTable = true;

      axios.get(`${apiBaseUrl}/progress/check`,{withCredentials:true}).then(res=>{
        if(res.data){
          if(res.data.status ==200){
            this.dataLoadingText = 'Calculating progress '+res.data.progress;
            if(res.data.progress == 100){
              this.checkForm();
            }
            else{
              setInterval( this.submitForm(),2000);
            }
          }
          if(res.data.status ==500){
            this.$message.error(res.data.progress)
            this.showTable = false;
            return;
          }
        }
      }).catch(err=>{
        this.$message.error(err)
        this.showTable = false;
      })
    },
    checkForm(){
      this.page.pageNum = 1;
      axios.get(`${apiBaseUrl}/data/query`,{withCredentials:true,params:{
        fileType:'outcome'
      }}).then(
        res=>{
          if(res.data){
            let d = res.data.score/7*100;
            this.percent = Number(Number(d).toFixed(2));
            
            this.progressText = res.data.category;
          }
        }).catch(err=>{
          this.$message.error(err);
        })
      axios.get(`${apiBaseUr}/data/query`,{withCredentials:true,params:{
        fileType:'variable'
      }}).then(res=>{
        if(res.data){
          let title = Object.keys(res.data);
          let d=[];
          let nowDate = new Date();
          for(let i in title){
          let innerTitle = Object.keys(res.data[title[i]])
          let innerValue = Object.values(res.data[title[i]])
          title[i] = title[i].slice(0,-5);
          //console.log(title[i])
          if(this.categoryType[title[i]]){
            for(let j in innerTitle){
              let mes={Variable:'',Categroy:'',CalculateTime:'',Value:''}
              mes.Categroy = title[i];
              mes.CalculateTime = moment(new Date().getTime()).format("YYYY-MM-DD HH:mm:ss");
              mes.Variable = innerTitle[j];
              mes.Value = innerValue[j];
              d.push(mes)
            }
          }
          //console.log(d)
          this.table1Org = d;
          this.queryTable1();
      }
        }
      }).catch(err=>{
        this.$message.error(err);
      })
      axios.get(`${apiBaseUrl}/data/query`,{withCredentials:true,params:{
        fileType:'feature'
      }}).then(res=>{
        if(res.data){
          let d = [];
          let title = Object.keys(res.data)
          for(let i in title){
            if(title[i]=='audio'){
              if(this.checkCategoryType.FeatureAudio){
                let mes = {Categroy:title[i],Tool:res.data[title[i]]['tool'],Frequency:res.data[title[i]]['frequency'],Size:res.data[title[i]]['size']}
                d.push(mes);
              }
            }
            if(title[i]=='vision'){
              if(this.checkCategoryType.FeatureImage){
                let mes = {Categroy:title[i],Tool:res.data[title[i]]['tool'],Frequency:res.data[title[i]]['frequency'],Size:res.data[title[i]]['size']}
                d.push(mes);
              }
            }
            if(title[i]=='text'){
              if(this.checkCategoryType.FeatureText){
                let mes = {Categroy:title[i],Tool:res.data[title[i]]['tool'],Frequency:res.data[title[i]]['frequency'],Size:res.data[title[i]]['size']}
                d.push(mes);
              }
            }
          }
          this.table2 = d;
        }
      }).catch(err=>{
        this.$message.error(err);
      })
      
      
    },
    format(percentage){
          if(percentage<30)return "Negative";
          if(percentage>=30 && percentage<70)return "Neutral";
          if(percentage>=70)return "Positive";
    },
    sendVideo(param){
      this.showFile = false;
      const formData = new FormData();
      formData.append('file',param.file);
      const url = `${apiBaseUrl}/video/upload`;
      
      axios.post(url,formData,{withCredentials: "true",}).then(res=>{
        //console.log(res.data.status)
        if(res.data.status == 200){
          console.log("upload success");
          this.showFile = true;
          this.$message.success("Upload Success")
        };
        if(res.data.status == 500){
          console.log("upload 500");
          this.$message.error(res.data.message)
        };
        if(res.data.status == 302){
          console.log(res.data)
        }
      }).catch(error=>{
        console.log("upload faild");
        this.$message.error(error)
      });
    },
    getCookie(){
      axios.get(`${apiBaseUrl}/video/submit`,{withCredentials: "true",}).then(res=>{
        console.log("submit success");
      }).catch(err=>{
        this.$message.error(err);
        console.log("submit faild")
      })
    },
    handleCurrentChange(pagenation){
      this.page.pageNum = pagenation;
      this.queryTable1()
      console.log(pagenation)
    },
    queryTable1(){
      this.table1 = []
      this.page.pageTotal = this.table1Org.length;
      for(let i=(this.page.pageNum-1)*(5);i<(this.page.pageNum-1)*5+5;i++){
        this.table1.push(this.table1Org[i])
      }
      this.dataIsLoading = false;
      console.log(this.table1);
      
    },
    resetForm(){
      this.categoryType = {AudioFeature:true,Denseflow:true,ImageBackground:true,FacialLandmark:true,FacialDescription:true,FeatureAudio:true,FeatureImage:true,FeatureText:true,IsAnalysis:true};
      this.checkCategoryType = {...this.categoryType};
    },
    
  }
}
</script>
